import blobStream from 'blob-stream';
import makerjs from 'makerjs';
import PDFDocument from 'pdfkit';

function mmToPDFPPI(mmDistance) {
  //pixel = dpi * mm / 25.4 mm (1 in)
  const PDFPPI = 72;
  return PDFPPI * mmDistance / 25.4;
}


function blobToBase64(blob) {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  return new Promise(resolve => {
    reader.onloadend = () => {
      resolve(reader.result);
    };
  });
};

class ModelRenderer {

    scaleModelToPDFPPI(model) {
      //STolen from: https://github.com/microsoft/maker.js/blob/b18f8a8d/packages/maker.js/src/core/pdf.ts#L11
      //try to get the unit system from the itemToExport
      var scale = 1;
      var exportUnits = 'mm';
      if (exportUnits) {
          //convert to inch
          scale = makerjs.units.conversionScale(exportUnits, makerjs.unitType.Inch);
      } else {
          //assume pixels, convert to inch
          scale = 1 / 100;
      }

      //from inch to PDF PPI
      scale *= 72;

      //TODO scale each element without a whole clone
      var scaledModel = makerjs.model.scale(makerjs.model.clone(model), scale);
      return scaledModel;
    }


    modelToPDF(doc,model,pageMargins,layerStyles) {
      //Ensure there is a styleset and it has a base styleset as minimum - as this will be the default
      if (!layerStyles) {
        layerStyles = {};
      }
      if (!layerStyles.base) {
        layerStyles.base = {
          strokeWidth: 1,
          strokeColor: '#000'
        }
      }
      //Not using PDFMake as it doesn't let us control pages
      let scaledModel = this.scaleModelToPDFPPI(model);
      //Apply Margins
      // makerjs.model.move(scaledModel,[100,100]);
      let modelExtents = makerjs.measure.modelExtents(scaledModel);

      let svgPaths = makerjs.exporter.toSVGPathData(scaledModel,{
        origin: [mmToPDFPPI(pageMargins.left),modelExtents.high[1]+mmToPDFPPI(pageMargins.top)],
        byLayers: true
      });
      for (const [key, value] of Object.entries(svgPaths)) {
        // components = components.concat(findComponentsInModelTrees(value));

        //Save the base style so we can restore to it later
        doc.save();
        doc.path(value);
        //Apply the style if the layer has one in styleset
        let style = layerStyles[((!key) || (key==''))?'base':key];
        if (!style) {style = layerStyles.base}

        if (style) {
          if (style.strokeWidth) {doc.lineWidth(style.strokeWidth);}
          if (style.dashed) {doc.dash(style.dashed[0],style.dashed[1]);} else {doc.undash()}
          if (style.strokeColor) {doc.stroke(style.strokeColor);}
        }


        //Restore if any changes to the styles
        doc.restore();
      }

      makerjs.model.getAllCaptionsOffset(scaledModel).forEach((modelCaption) => {
        console.log('Processing',modelCaption);
        doc.text(
          modelCaption.text,
          modelExtents.low[0] + mmToPDFPPI(pageMargins.left) + modelCaption.anchor.origin[0],
          (modelExtents.high[1] + mmToPDFPPI(pageMargins.top)) - modelCaption.anchor.origin[1] - ((modelCaption.anchor.end[1] - modelCaption.anchor.origin[1])/2),
          {
            baseline: 'top'
          }
        );
        console.log('Processing',modelCaption,modelExtents, mmToPDFPPI(pageMargins.top),modelExtents.low[0] + mmToPDFPPI(pageMargins.left) + modelCaption.anchor.origin[0],(modelExtents.high[1] + mmToPDFPPI(pageMargins.top)) - modelCaption.anchor.origin[1]);
      });


    }




    pdfRender(pageModels,renderOpts) {
      let renderOptions = {
        styleSet: {
          base: {
            strokeWidth: '1',
            strokeColor: '#000'
          },
          center: {
            strokeWidth: '1',
            dashed: [10,20],
            strokeColor: '#0F0'
          },
          hint: {
            strokeWidth: '1',
            dashed: [5,5],
            strokeColor: '#00F'
          },
          stitch: {
            strokeWidth: '1',
            dashed: [2,10],
            strokeColor: '#F000'
          },
          patternJoinLine: {
            strokeWidth: '1',
            dashed: [2,10],
            strokeColor: '#b2c'
          },
        },
        pageMargins: 10,
        units: 'mm',
        layout: 'portrait',
        pageSize: 'A4',

      }

      Object.assign(renderOptions,renderOpts);

      //Do some sanity checking
      if (!renderOptions.pageSize) {
        throw 'Page size must be specified for PDF rendering, try A4'
      }

      if (renderOptions.pageMargins) {
        //force pageMargins to be an object for simplicity later`
        //Model packer should have taken care of this for us
        if (typeof renderOptions.pageMargins != "object") {
          renderOptions.pageMargins = {
            left: renderOptions.pageMargins,
            right: renderOptions.pageMargins,
            top: renderOptions.pageMargins,
            bottom: renderOptions.pageMargins
          }
        }
      }

      //ction createPagesAsPDF(pages,marginTopLeft = [0,0],styleSet = null,pdfAction){
      //
      let pdfPageSettings = {
        layout: renderOptions.layout,
        //if the pageSize is a string assume its A4,A5 or similar, otherwise assume its page dims object with x and y
        size: ((typeof renderOptions.pageSize == 'string')?(renderOptions.pageSize):([mmToPDFPPI(renderOptions.pageSize.x),mmToPDFPPI(renderOptions.pageSize.y)]))
      }

      console.log('PDF Setup',pdfPageSettings);
      let doc = new PDFDocument({
        autoFirstPage: false,
      });

      let stream = doc.pipe(blobStream());
      pageModels.forEach(page => {
        doc.addPage(pdfPageSettings);

        // makerjs.exporter.toPDF(doc,page,pdfExportOptions);
        this.modelToPDF(doc,page,renderOptions.pageMargins,renderOptions.styleSet);
      });

      // Finalize PDF file
      doc.end();
      // get a blob when you're done



      let pdfCompleted = new Promise(function(resolve, reject) {
          stream.on('finish', function() {
            // get a blob you can do whatever you like with
            const blob = stream.toBlob('application/pdf');
            resolve(blobToBase64(blob));
          });

          stream.on('error', function() {
            reject()
          });
      });
      return pdfCompleted;
    }

    svgRender(models, renderOpts) {
      let renderOptions = {
        styleSet: {
            stitch: {
              stroke: 'red',
              strokeWidth: '1',
              cssStyle: 'stroke-dasharray: 5, 5'
            },
            center: {
              stroke: 'green',
              strokeWidth: '1',
              cssStyle: 'stroke-dasharray: 5, 5, 12, 5'
            },
            hint: {
              stroke: 'blue',
              strokeWidth: '1',
              cssStyle: 'stroke-dasharray: 2, 8'
            },
          }
      }
      Object.assign(renderOptions,renderOpts);

      let svgPages = models.map(packedModel => {
        packedModel.units = makerjs.unitType.Millimeter; //Metric FTW
        return makerjs.exporter.toSVG(packedModel,{
          useSvgPathOnly: false,
          units: "mm",
          layerOptions: renderOptions.styleSet
        });
      });

      return Promise.resolve(svgPages);
    }

    render(model,renderOpts) {
      let renderOptions = {
        format: 'SVG'
      }
      Object.assign(renderOptions,renderOpts);

    }
}


module.exports = ModelRenderer;
