import makerjs from 'makerjs';
import codeRunner from './coderunner.js';
import ModelPacker from './modelpacker.js';
import ModelRender from './modelrenderer.js';


onmessage = function(e) {
  console.log('Worker: Message received from main script');


  let drawCode = e.data.drawCode;
  let userParams = e.data.userParams;
  let renderOptions = e.data.renderOptions;
  let splitterOptions = e.data.splitterOptions;
  let returnAction = e.data.returnAction;
  let requestId = e.data.requestId;

  let codeRunnerResult = codeRunner(drawCode,userParams);
  let modelOutput = codeRunnerResult.output;

  if (modelOutput) {
    let modelPacker = new ModelPacker();

    let packerOutput = modelPacker.packToPages(modelOutput,(renderOptions.paged?renderOptions.pageSize:null),renderOptions);

    let modelRenderer = new ModelRender();
    //Packer should alrways return //something//
    let outputFit = [];
    let outputNoFit = [];
    let actualOutputFormat;

    if (renderOptions.renderMode == 'PDF') {
      //Both renders need to return a promise as PDFKIT uses streams so this async needs to be managed
      outputFit = modelRenderer.pdfRender(packerOutput.pages,renderOptions);
      outputNoFit = Promise.resolve(null);
      actualOutputFormat = 'PDF';
    } else {
      //Asssume SVG as default
      outputFit = modelRenderer.svgRender(packerOutput.pages,renderOptions);
      outputNoFit = modelRenderer.svgRender(packerOutput.rejected,renderOptions);
      actualOutputFormat = 'SVG';
    }

    Promise.all([outputFit,outputNoFit]).then((values) => {
      let pdfdf = btoa(values[0]);
      let renderOutput = {
        messageType: 'renderOutput',
        output: values[0],
        issues: {
          noFit: values[1],
          error: []
        },
        userParameters: modelOutput.userParameters,
        returnAction: returnAction,
        requestId: requestId,
        format: actualOutputFormat
      }

      postMessage(renderOutput);
    });


  } else {
    let renderOutput = {
      messageType: 'renderOutput',
      output: null,
      issues: {
        noFit: [],
        error: []
      },
      returnAction: returnAction,
      requestId: requestId,
      format: 'SVG'
    }

    postMessage(renderOutput);
  }




}
