import makerjs from 'makerjs';
import { EmptyModel } from './modelbasics.js';

export class ModelSplitter  {

  trimModelToLine(modelToSlice,lineAt,options) {
    let appliedOptions = Object.assign({
      keepPositiveSide: true,
      alongXAxis: true,
      overlap: 5,
      includeCuttingLineHint: true,
      asClone: true,
      cutHintLineExtension: 5
    },options);
    //Clone or not the model
    if (appliedOptions.asClone) {
      modelToSlice = makerjs.model.clone(modelToSlice);
    }

    let modelSize = makerjs.measure.modelExtents(modelToSlice);
    console.log('Cutting');

    //Create the cutter pieces
    let cutterPath, boundingBox;
    let boundingBoxWidth, boundingBoxOffsetX, boundingBoxHeight, boundingBoxOffsetY;

    if (appliedOptions.alongXAxis) {
      cutterPath = new makerjs.paths.Line(
        [modelSize.low[0]+lineAt+(appliedOptions.keepPositiveSide?-appliedOptions.overlap:appliedOptions.overlap),modelSize.low[1] - (modelSize.height*0.1)],
        [modelSize.low[0]+lineAt+(appliedOptions.keepPositiveSide?-appliedOptions.overlap:appliedOptions.overlap),modelSize.high[1] + (modelSize.height*0.1)]
      );

      boundingBoxWidth = (appliedOptions.keepPositiveSide?(modelSize.width-lineAt):(lineAt)) + (appliedOptions.overlap*2);
      boundingBoxOffsetX = (appliedOptions.keepPositiveSide?(modelSize.low[0]+lineAt-appliedOptions.overlap):(modelSize.low[0]-appliedOptions.overlap));
      boundingBoxHeight = modelSize.height + (appliedOptions.overlap*2);
      boundingBoxOffsetY = modelSize.low[1] - appliedOptions.overlap;
    } else {
      cutterPath = new makerjs.paths.Line(
        [modelSize.low[0] - (modelSize.height*0.1), modelSize.low[1]+lineAt+(appliedOptions.keepPositiveSide?-appliedOptions.overlap:appliedOptions.overlap)],
        [modelSize.high[0] + (modelSize.height*0.1), modelSize.low[1]+lineAt+(appliedOptions.keepPositiveSide?-appliedOptions.overlap:appliedOptions.overlap)]
      );

      boundingBoxWidth = modelSize.width + (appliedOptions.overlap*2);
      boundingBoxOffsetX = modelSize.low[0] - appliedOptions.overlap;
      boundingBoxHeight = (appliedOptions.keepPositiveSide?(modelSize.height-lineAt):(lineAt)) + (appliedOptions.overlap*2);
      boundingBoxOffsetY = (appliedOptions.keepPositiveSide?(modelSize.low[0]+lineAt-appliedOptions.overlap):(modelSize.low[0]-appliedOptions.overlap));
    }
    boundingBox = makerjs.model.move(
      new makerjs.models.Rectangle(boundingBoxWidth,boundingBoxHeight),
      [boundingBoxOffsetX,boundingBoxOffsetY]
    );
    console.log(modelSize,boundingBoxOffsetX,boundingBoxWidth,boundingBoxOffsetY,boundingBox,boundingBoxHeight);

    //Jsut break the paths apart to start with
    makerjs.model.walk(modelToSlice,{
      onPath: (context) => {
        let pathIntersection = makerjs.path.intersection(context.pathContext,cutterPath);
        if (pathIntersection){
          pathIntersection.intersectionPoints.forEach(intersectionPoint => {
            let addPath = makerjs.path.breakAtPoint(context.pathContext,intersectionPoint);
            makerjs.model.addPath(context.modelContext,addPath);
          });
        }
      }
    });
    // //Now walk again trimming items that aren't in the bounding box
    makerjs.model.walk(modelToSlice,{
      onPath: (context) => {
        if (!makerjs.model.isPathInsideModel(context.pathContext,boundingBox,context.offset)) {
          delete context.modelContext.paths[context.pathId]
        }
      }
    });

    //Add the cut line hint line - ignoring overlap
    if (appliedOptions.includeCuttingLineHint) {
      let cuttingLineHint;
      if (appliedOptions.alongXAxis) {
        cuttingLineHint = new makerjs.paths.Line(
          [modelSize.low[0]+lineAt,modelSize.low[1] - (appliedOptions.cutHintLineExtension)],
          [modelSize.low[0]+lineAt,modelSize.high[1] + (appliedOptions.cutHintLineExtension)]
        );
      } else {
        cuttingLineHint = new makerjs.paths.Line(
          [modelSize.low[0] - (appliedOptions.cutHintLineExtension), modelSize.low[1]+lineAt],
          [modelSize.high[0] + (appliedOptions.cutHintLineExtension), modelSize.low[1]+lineAt]
        );
      }
      makerjs.path.layer(cuttingLineHint,'patternJoinLine');
      makerjs.model.addPath(modelToSlice,cuttingLineHint);
    }

    return modelToSlice
  }


  splitModel(modelToSplit,splitAtPct = .5,options) {
    let appliedOptions = Object.assign({
      alongXAxis: true,
    },options);

    let modelExtents = makerjs.measure.modelExtents(modelToSplit);
    let splitAtDist = appliedOptions.alongXAxis?(splitAtPct *  modelExtents.width):(splitAtPct *  modelExtents.height);
    console.log('splitting at',splitAtDist);
    let positiveSide = this.trimModelToLine(modelToSplit,splitAtDist,Object.assign({},appliedOptions,{keepPositiveSide:true}));
    let negativeSide = this.trimModelToLine(modelToSplit,splitAtDist,Object.assign({},appliedOptions,{keepPositiveSide:false}));
    return {
      models: {
        a: positiveSide,
        b: negativeSide
      }
    }
  }

}
